import React, { Component } from 'react'
import Newsitem from './Newsitem'

export default class News extends Component {
    constructor() {
        super();
        this.state = {
            articles: [],
            loading: false,
            page: 1,
            total:null
        }
    }
    async componentDidMount() {
        let url = `https://newsapi.org/v2/top-headlines?country=in&category=business&apiKey=af183ac4ad884c598249f4a9ae1ae2cf&page=1&pageSize=10`
        let data = await fetch(url)
        let parseddata = await data.json();
        this.setState({ articles: parseddata.articles,total:parseddata.totalResults})
    }
    Next = async() => {
        if(this.state.page>Math.ceil(this.state.total/10))
        {
        }
        else
        {
        let url = `https://newsapi.org/v2/top-headlines?country=in&category=business&apiKey=af183ac4ad884c598249f4a9ae1ae2cf&page=${this.state.page + 1}&pageSize=10`
        let data = await fetch(url)
        let parseddata = await data.json();
        this.setState({
            page: this.state.page + 1,
            articles: parseddata.articles
        })
    }
    }
    Prev = async() => {
        let url = `https://newsapi.org/v2/top-headlines?country=in&category=business&apiKey=af183ac4ad884c598249f4a9ae1ae2cf&page=${this.state.page - 1}&pageSize=10`
        let data = await fetch(url)
        let parseddata = await data.json();
        this.setState({
            page: this.state.page - 1,
            articles: parseddata.articles
        })
    }
    render() {
        return (
            <div className="container my-3">
                <h2>News Donkey - These are top headlines today</h2>
                <div className="row">
                    {this.state.articles.map((element) => {
                        return <div className="col-md-4" key={element.url}>
                            <Newsitem title={element.title ? element.title.slice(0, 45) : " "} description={element.description ? element.description.slice(0, 97) : " "} imgurl={element.urlToImage} url={element.url} />
                        </div>
                    })}
                    <div className="d-flex justify-content-between">
                        <button type="button" disabled={this.state.page <= 1} onClick={this.Prev} className="btn btn-dark">&larr; Previous</button>
                        <button type="button" disabled={this.state.page >= Math.ceil(this.state.total/10)} onClick={this.Next} className="btn btn-dark">Next &rarr;</button>
                    </div>

                </div>
            </div>
        )
    }
}
